* {
    margin: 0;
    padding: 0;
    /* font-family: 'Raleway', sans-serif; */
    /* font-family: 'Rubik', sans-serif; */
    font-family: 'Jost', sans-serif;
}

:root {
    --bg-color: #62ad00;
    --bg-color-hower: #508e00;
    --theme-green:#43A047;

}



a {
    text-decoration: none;
    color: black;
}


.navbar_container {
    height: 80px;
    background-color: #f0f0f0;
    padding: 0px 5vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.main_nav {
    width: 100%;
    height: 100%;
    display: flex;
    /* padding: 0px 15px; */
    align-items: center;
}

.navbar_btn {
    border: none;
    padding: 10px 15px;
    border-radius: 4px;
    color: white;
    background-color: var(--bg-color);
    transition: all 0.2s;

}

.navbar_btn:hover {

    background-color: var(--bg-color-hower);
}

.navbar__toggle {
    font-size: 28px;
    cursor: pointer;
    outline: none;
    background-color: rgb(255, 255, 255);
    border: none;
    padding: 5px;
    color: rgb(0, 0, 0);
    z-index: 10000;
    /* margin-right: 20px; */
}



.navbar__logo,
.navbar__toggle {
    display: none;
}


.nav__content {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;

    justify-content: space-between;
}


.main_nav_logo {
    width: 200px;
    margin-left: 20px;

}

.main_nav_logo img {
    width: 100%;
    height: 100%;
}



.nav__list {
    display: flex;
    align-items: center;
}

.nav__item {
    align-items: center;
    margin-left: 0px;
    padding: 28px 10px;
    line-height: 22.5px;
    font-size: 16px;
    color: black;
    /* background-color: #0e81cf; */
}



.nav__item:hover {
    color: var(--bg-color);

}

.dropdown_nav {
    display: flex;
    align-items: center;
}

.dropdown_nav i {
    /* font-size: 10px; */
    padding: 5px;

}

.dropdown_content {
    display: none;
    position: absolute;
    top: 78px;
    z-index: 10;
    background-color: white;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    border-radius: 3px 3px 0px 0px;
    border-top: 2px solid #6cc000;
    width: 220px;
    padding: 10px 0px;
}

.dropdown_link {
    padding: 10px 20px;
    width: 100%;
    display: inline-block;
    font-size: 16px;
}

.dropdown_link:hover {
    background-color: rgba(178, 175, 175, 0.2);
}

.dropdown_link {
    color: black;
}

.dropdown_link:hover {
    color: var(--bg-color);
}

.dropdown_nav:hover+.dropdown_content {
    display: block;
}

.dropdown_content:hover {
    display: block;
}





