  }





.alert_container {
    position: fixed;
    top: 12vh;
    right: 0px;
    min-width: 20vw;
    /* color: rgb(0, 43, 63);
      background-color: #6ed5f7; */
    color: rgb(0, 0, 0);
    background-color: #FFC107;
    padding: 15px 25px;
    border-radius: 5px 0px 0px 5px;
    opacity: 1;
    visibility: visible;
    animation: fade-in 0.5s ease-out forwards;
    z-index: 1000000000;
  }
  
  .alert_container.hide-alert {
    animation: fade-out 0.5s ease-in forwards;
  }
  
  @keyframes fade-in {
    from {
      opacity: 0;
      transform: translateX(50px);
    }
  
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
  
  @keyframes fade-out {
    from {
      opacity: 1;
      transform: translateX(0);
    }
  
    to {
      opacity: 0;
      /* transform: translateX(50px); */
    }
  }

  




